import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function NavStyle(){
    setDarkLightMode();
    setStyle();
    window.addEventListener('resize', setStyle);
}

export default NavStyle;

function setDarkLightMode(){
    darkLightMode('quickScan');
    darkLightMode('quickScanBtn');
    darkLightMode('logout');
}

function setStyle(){
    eId('nav').style.height = eId('app').offsetHeight * 0.12 + 'px';
    if(window.innerWidth <= 576){
        eClass('Nav')[0].className = eClass('Nav')[0].className.replace(' px-4', ' px-3');
        eClass('quickScan')[0].style.width = eId('nav').offsetHeight * 0.8 + 'px';
    } else {
        eClass('Nav')[0].className = eClass('Nav')[0].className.replace(' px-3', ' px-4');
        eClass('quickScan')[0].style.width = eId('nav').offsetHeight * 0.7 + 'px';
    }

    eClass('quickScan')[0].style.height = eClass('quickScan')[0].offsetWidth + 'px';
    eClass('quickScanBtn')[0].style.width = eId('quickScan').offsetHeight * 0.85 + 'px';
    eClass('quickScanBtn')[0].style.height = eClass('quickScanBtn')[0].offsetWidth + 'px';
}