import SubmitDataHelpers from '../helpers/SubmitDataHelpers.js';
import CookieHelpers from '../helpers/CookieHelpers';

function Logout(){
    let helpers = new SubmitDataHelpers;

    helpers.postData('/authentication/logout/', {})
    .then((res) => {
        //console.log(res);
        if(window.location.pathname.replace(window.sessionStorage.path, '/') !== '/'){
            let now = Date.now();
            let expiredDate = new Date(now - 3600);
            expiredDate = expiredDate.toUTCString();
        
            let cookieHelpers = new CookieHelpers;
            cookieHelpers.setCookie("username", res.username, expiredDate, "/");
            cookieHelpers.setCookie("activeId", res.activeId, expiredDate, "/");

            window.sessionStorage.clear();
            window.localStorage.clear();
            window.location.href = window.sessionStorage.path;
        }
    });

    return null;
}

export default Logout;