import React from 'react';
import Logout from '../authentication/Logout';
import NavStyle from '../../css/Layout/NavStyle';
import '../../css/Layout/Nav.css';

class Nav extends React.Component{
    componentDidMount(){
        NavStyle();
    }

    componentDidUpdate(){
        NavStyle();
    }

    render(){
        switch(window.location.pathname.replace(window.sessionStorage.path, '/')){
            case '/':
            case '/home/':
            case '/login/':
            case '/register/':
            case '/guest/':
                //console.log('ok');
                return null;
            default:
                return this.renderNav();
        }
    }

    renderNav(){
        return(
            <div id="nav" className="Nav">
                <button className='goBack' onClick={()=>window.history.back()}><i className="fas fa-chevron-left"> Go back</i></button>

                <div id="quickScan" className="quickScan p-1 rounded-circle shadow"
                    data-toggle="tooltip" data-placement="top" title="Quick Scan..."
                >
                    <a href={window.sessionStorage.path + "guest/scan/"}>
                        <button className="btn quickScanBtn rounded-circle"><i className="fas fa-camera"></i></button>
                    </a>
                </div>
                
                <button className='logout' onClick={()=>Logout()}><i className="fas fa-power-off"> Log out</i></button>
            </div>
        );
    }
}
 export default Nav;